
import { defineComponent, reactive } from 'vue';
import { getDotNetwork } from '@/services/GetNetworkService';
import settings from '@/assets/settings.json';
import { compressToEncodedURIComponent } from 'lz-string';

//参考HP
//https://arrown-blog.com/vuejs-dropdownmenu/
//https://tedate.jp/vue-js/how-to-changing-the-data-part-in-vue3

export default defineComponent({
  name: 'Network',
  props: {
    panel_fmid: String,
    url_pathTop: String,
    defect_fmid: String
  },
  setup(props){
    const data = reactive({
      panel_name:"",
      defect_fmid:"",
      fuguaiNaiyou:"",
      src:"",
      error: {
        title:"",
        message:[] as string[]
      }
    })


    //画面描画
    getDotNetwork((props.panel_fmid) as string, (props.url_pathTop) as string, (props.defect_fmid) as string).then(([panel_name, fuguaiNaiyou, dot, ex]) => {
      if (panel_name && panel_name !=='Error') {
        // 画面タイトル設定
        data.panel_name = panel_name;

        // 不具合情報設定
        if(props.defect_fmid) {
          data.defect_fmid = (props.defect_fmid) as string;
          data.fuguaiNaiyou = fuguaiNaiyou;
        }

        // graphviz描画URL作成
        let digraph = compressToEncodedURIComponent(dot);
        const src = settings.graphvizUrl + '?digraph=' + digraph;
        data.src = src;
      } else if (!panel_name) {
        data.error.title = settings.notFound.title;
        settings.notFound.message.forEach((value:string) => {
          data.error.message.push(value);
        });
        data.error.message.push('・エラーコード：E001');
        data.error.message.push(`・URLパラメータ1：${props.url_pathTop}`);
        data.error.message.push(`・FMID：${props.panel_fmid}`);
      } else {
        data.error.title = settings.dataException.title;
        settings.dataException.message.forEach((value:string) => {
          data.error.message.push(value);
        });
        data.error.message.push('・エラーコード：E002');
        data.error.message.push(`・URLパラメータ1：${props.url_pathTop}`);
        data.error.message.push(`・FMID：${props.panel_fmid}`);
        data.error.message.push(`・内容：${ex}`);
      }
    });

    /* iframeのサイズを自動調整しようとしたが、厳しい
    onMounted(() => {
      const element = document.getElementById("graphviz")!; 
      element.style.height = "200px";
    })
    */
    return{
      data
    }
  }
});
