import axios from "axios";
import settings from '@/assets/settings.json';
import { getDotDefect } from '@/services/GetDefectService';

type Connection = {parent_nm: string; child_nm: string; edge: string;};
type ConnectionList = {[key: string]: Connection;}; //key=parent_nm + child_nm + Edge
type NodeList = {[key: string]: string;}; //key=parent_nm or child_nm、value=parent or child
export async function getDotNetwork(panel_fmid: string, type: string, defect_fmid?: string): Promise<[string, string, string, any]> {
    try {
        // SPARQLのbaseURLを取得
        const baseUrl = settings.sparqlUrl;

        // クエリ、URI生成
        let queryUp = "";
        let queryDown = "";
        if (type == "obj") {
            queryUp = getQueryObjUp(panel_fmid);
            queryDown = getQueryObjDown(panel_fmid);
        } else if (type == "tree") {
            queryUp = getQueryTreeUp(panel_fmid);
            queryDown = getQueryTreeDown(panel_fmid);
        } else {
            throw new Error("URIパスが存在しません。");
        }

        const urlUp = baseUrl + '?query=' + encodeURIComponent(queryUp);
        const urlDown = baseUrl + '?query=' + encodeURIComponent(queryDown);
  
        // APIを呼び出す
        const responseUp = await axios.get(urlUp);
        const responseDown = await axios.get(urlDown);

        // 取得結果を設定（全てのコネクションとノードのリストを作成）
        let tgt_node = '';
        let resDataConnectionList:ConnectionList = {};
        let resDataNodeList:NodeList = {};
        [tgt_node, resDataConnectionList, resDataNodeList] = addData(responseUp.data.results.bindings, tgt_node, resDataConnectionList, resDataNodeList);
        [tgt_node, resDataConnectionList, resDataNodeList] = addData(responseDown.data.results.bindings, tgt_node, resDataConnectionList, resDataNodeList);

        // 不具合情報を取得（ネットワークが取得できている場合）
        let dotDefect = '';
        let fuguaiNaiyou = '';
        if(tgt_node && defect_fmid)
        {
            await getDotDefect(defect_fmid).then(([_fuguaiNaiyou, _dotDefect, _ex]) => {
                if (_fuguaiNaiyou && _fuguaiNaiyou =='Error') {
                    throw new Error(_ex);
                } else {
                    fuguaiNaiyou = _fuguaiNaiyou;
                    dotDefect = _dotDefect;
                }
            });
        }

        const dot = createDot(tgt_node, resDataConnectionList, resDataNodeList, dotDefect);

        return [tgt_node, fuguaiNaiyou, dot, ''];
    } catch (ex) {
        return ['Error', '', '', ex];
    }
}

function addData(data:any, tgt_node:string, resDataConnectionList:ConnectionList, resDataNodeList:NodeList): [string, ConnectionList, NodeList]{
    data.forEach((value:any) => {
        if(!tgt_node){
            tgt_node = value['tgt_nm']['value'];
        }

        const parent_nm = value['parent_nm']['value'];
        const child_nm = (value['child_nm'])?value['child_nm']['value']:value['child']['value'];
        let parent_uri = (value['parent_is_uri']['value'] == 1)?value['parent']['value']:'';
        parent_uri = convertUri(parent_uri);
        let child_uri = (value['child_is_uri']['value'] == 1)?value['child']['value']:
                            (value['child'] && value['child']['value'].startsWith('http'))?value['child']['value']:'';
        child_uri = convertUri(child_uri);
        const edge = (value['proc_nm'])?value['proc_nm']['value']:'';

        if(!((parent_nm + child_nm + edge) in resDataConnectionList))
        {
            resDataConnectionList[(parent_nm + child_nm + edge)] = {parent_nm: parent_nm, child_nm: child_nm, edge: edge};

            if(!(parent_nm in resDataNodeList))
                resDataNodeList[parent_nm] = parent_uri;
            if(!(child_nm in resDataNodeList))
                resDataNodeList[child_nm] = child_uri;
        }
    });

    return [tgt_node, resDataConnectionList, resDataNodeList];
}

function createDot(tgt_node:string, resDataConnectionList:ConnectionList, resDataNodeList:NodeList, dotDefect:string): string{
    let dot = `digraph g {\n`;
    dot += `rankdir=LR\n`;
    dot += `node [style=filled,color=red];\n`;
    dot += `"${tgt_node}";\n`;
    dot += `node [style=filled,color=lightgrey];\n`;
    for (const connection of Object.values(resDataConnectionList))
        dot += `"${connection.parent_nm}" -> "${connection.child_nm}" [label="${connection.edge}"]\n`;
    for (const node of Object.keys(resDataNodeList))
        dot += `"${node}" [URL="${resDataNodeList[node]}"]\n`;
    dot += dotDefect;
    dot += `}`;

    return dot;
}

function convertUri(uri:string): string{
    // Graphvizで扱えるように、URIをエンコード
    try {
        // URIをデコード（エンコード済みURIへの対応）
        const decodedUri = decodeURI(uri);
        // URIをエンコード
        const encodedUri = encodeURI(decodedUri);
        // GraphvizのURIは「XML」に準ずるため、XMLのエスケープを実施
        const convertedUri = encodedUri.replaceAll('&','&amp;').replaceAll('>','&gt;').replaceAll('<','&lt;').replaceAll('\'','&apos;').replaceAll('"','&quot;');

        return convertedUri;
    } catch (err) {
        // URLでない場合は、何もしない
        return uri;
    }
}

function getQueryObjUp(fmid:string): string{
    const query = `
        PREFIX brick: <https://brickschema.org/schema/Brick#>
        PREFIX rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>
        PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>

        select distinct ?tgt ?tgt_nm ?parent_nm ?proc_nm ?child_nm ?parent ?child ?parent_tp_nm ?child_tp_nm ?parent_is_uri ?child_is_uri where{
            #検索対象を取得
            ?child <http://www.semanticweb.org/chensisi/ontologies/2022/9/untitled-ontology-163:OWLObjectProperty_c91c8dae_12f9_45ab_ac9e_35a81e8c9dc3> "${fmid}"@ja. #FMID：検索キー
            
            #再帰的に検索対象の下流のオブジェクトを検索（全種類の述語）
            ?parent ?proc ?child.
            ?parent rdfs:label ?parent_nm.
            ?child rdfs:label ?child_nm.
            ?proc rdfs:label ?proc_nm.
            
            #上流側要素のタイプ
            ?parent a ?parent_tp.
            ?parent_tp rdfs:label ?parent_tp_nm.
        
            #下流側要素のタイプ
            ?child a ?child_tp.
            ?child_tp rdfs:label ?child_tp_nm.
        
            #URIかどうか確認
            BIND (isURI(?parent) AS ?parent_is_uri)
            BIND (isURI(?child) AS ?child_is_uri)
        
            #tgtに値を設定
            BIND (?child AS ?tgt)
            ?tgt rdfs:label ?tgt_nm.
        }
    `;

    return query;
}

function getQueryObjDown(fmid:string): string{
    const query = `
        PREFIX brick: <https://brickschema.org/schema/Brick#>
        PREFIX rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>
        PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>
        
        select distinct ?tgt ?tgt_nm ?parent_nm ?proc_nm ?child_nm ?parent ?child ?parent_tp_nm ?child_tp_nm ?parent_is_uri ?child_is_uri where{
            #検索対象を取得
            ?parent <http://www.semanticweb.org/chensisi/ontologies/2022/9/untitled-ontology-163:OWLObjectProperty_c91c8dae_12f9_45ab_ac9e_35a81e8c9dc3> "${fmid}"@ja. #FMID：検索キー
        
            #再帰的に検索対象の下流のオブジェクトを検索（全種類の述語）
            ?parent ?proc ?child.
            ?parent rdfs:label ?parent_nm.
            OPTIONAL {?child rdfs:label ?child_nm.} #?childが文字列の場合は?child_nmが取れないので、任意とした
            ?proc rdfs:label ?proc_nm.
            
            #上流側要素のタイプ
            ?parent a ?parent_tp.
            ?parent_tp rdfs:label ?parent_tp_nm.
            
            #下流側要素のタイプ #?childが文字列の場合は?child_tpが取れないので、任意とした
            OPTIONAL {
                ?child a ?child_tp.
                ?child_tp rdfs:label ?child_tp_nm.
            }
            
            #URIかどうか確認
            BIND (isURI(?parent) AS ?parent_is_uri)
            BIND (isURI(?child) AS ?child_is_uri)
            
            #tgtに値を設定
            BIND (?parent AS ?tgt)
            ?tgt rdfs:label ?tgt_nm.
        }
    `;

    return query;
}

function getQueryTreeUp(fmid:string): string{
    const query = `
        PREFIX brick: <https://brickschema.org/schema/Brick#>
        PREFIX rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>
        PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>
        
        select distinct ?tgt ?tgt_nm ?parent_nm ?proc_nm ?child_nm ?parent ?child ?parent_tp_nm ?child_tp_nm ?parent_is_uri ?child_is_uri where{
            #電灯盤取得
            ?tgt <http://www.semanticweb.org/chensisi/ontologies/2022/9/untitled-ontology-163:OWLObjectProperty_c91c8dae_12f9_45ab_ac9e_35a81e8c9dc3> "${fmid}"@ja. #FMID：検索キー
            ?tgt rdfs:label ?tgt_nm.
        
            #再帰的に電灯盤の上流のオブジェクトを検索（hasPartsまたはfeedsで結びつくもの）
            ?parent (brick:hasPart|brick:feeds)+ ?tgt. #1回以上再帰的に検索
            ?child (brick:hasPart|brick:feeds)* ?tgt. #0回以上再帰的に検索
            ?parent rdfs:label ?parent_nm.
            ?child rdfs:label ?child_nm.
            ?parent ?proc ?child.
            ?proc rdfs:label ?proc_nm.
            
            #上流側要素のタイプ
            ?parent a ?parent_tp.
            ?parent_tp rdfs:label ?parent_tp_nm.
            
            #下流側要素のタイプ
            ?child a ?child_tp.
            ?child_tp rdfs:label ?child_tp_nm.
                
            #URIかどうか確認
            BIND (isURI(?parent) AS ?parent_is_uri)
            BIND (isURI(?child) AS ?child_is_uri)
        }
    `;

    return query;
}

function getQueryTreeDown(fmid:string): string{
    const query = `
        PREFIX brick: <https://brickschema.org/schema/Brick#>
        PREFIX rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>
        PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>
        
        select distinct ?tgt ?tgt_nm ?parent_nm ?proc_nm ?child_nm ?parent ?child ?parent_tp_nm ?child_tp_nm ?parent_is_uri ?child_is_uri where{
            #電灯盤取得
            ?tgt <http://www.semanticweb.org/chensisi/ontologies/2022/9/untitled-ontology-163:OWLObjectProperty_c91c8dae_12f9_45ab_ac9e_35a81e8c9dc3> "${fmid}"@ja. #FMID：検索キー
            ?tgt rdfs:label ?tgt_nm.
        
            #再帰的に電灯盤の配下のオブジェクトを検索（hasPartsまたはfeedsで結びつくもの）
            ?tgt (brick:hasPart|brick:feeds)* ?parent. #0回以上再帰的に検索
            ?tgt (brick:hasPart|brick:feeds)+ ?child. #1回以上再帰的に検索
            ?parent rdfs:label ?parent_nm.
            ?child rdfs:label ?child_nm.
            ?parent ?proc ?child.
            ?proc rdfs:label ?proc_nm.
            
            #上流側要素のタイプ
            ?parent a ?parent_tp.
            ?parent_tp rdfs:label ?parent_tp_nm.
            
            #下流側要素のタイプ
            ?child a ?child_tp.
            ?child_tp rdfs:label ?child_tp_nm.
                
            #URIかどうか確認
            BIND (isURI(?parent) AS ?parent_is_uri)
            BIND (isURI(?child) AS ?child_is_uri)
        }
    `;

    return query;
}