import axios from "axios";
import settings from '@/assets/settings.json';

type FuguaiList = {[key: string]: string[];}; //key=fuguai_nm、value=eikyou_nm
export async function getDotDefect(defect_fmid: string): Promise<[string, string, any]> {
    try {
        // 不具合情報部分のdot作成。他（系統ネットワークなど）から呼び出すことで使用

        // SPARQLのbaseURLを取得
        const baseUrl = settings.sparqlUrl;

        // クエリ、URI生成
        const query = getQueryDefect(defect_fmid);
        const url = baseUrl + '?query=' + encodeURIComponent(query);
  
        // APIを呼び出す
        const response = await axios.get(url);

        // 取得結果を設定（全てのコネクションとノードのリストを作成）
        let fuguaiNaiyou = '';
        let koshouList:string[] = [];
        let fuguaiList:FuguaiList = {};
        let konponGeninList:string[] = [];
        [fuguaiNaiyou, koshouList, fuguaiList, konponGeninList] = addData(response.data.results.bindings);

        const dot = createDot(koshouList, fuguaiList, konponGeninList);

        return [fuguaiNaiyou, dot, ''];
    } catch (ex) {
        return ['Error', '', ex];
    }
}

function addData(data:any): [string, string[], FuguaiList, string[]]{
    let resFuguaiNaiyou = '';
    const resDataKoshouList:string[] = [];
    const resDataFuguaiList:FuguaiList = {};
    const resDataKonponGeninList:string[] = [];

    data.forEach((value:any) => {

        if(!resFuguaiNaiyou && value['fuguai_naiyou_val']){
            resFuguaiNaiyou = value['fuguai_naiyou_val']['value'];
        }

        // 値が取得できていて未登録の場合、追加
        if(value['koshou_nm'] && resDataKoshouList.indexOf(value['koshou_nm']['value']) == -1){
            resDataKoshouList.push(value['koshou_nm']['value'])
        }

        const fuguai_nm = (value['fuguai_nm'])?value['fuguai_nm']['value']:'';
        // 値が取得できていて未登録の場合または、「影響」があり未登録の場合（不具合認知がnullの場合。影響登録時に追加されるが、処理統一）、追加
        if((value['fuguai_nm'] && !(fuguai_nm in resDataFuguaiList)) || (value['eikyou_nm'] && !(fuguai_nm in resDataFuguaiList))){
            resDataFuguaiList[fuguai_nm] = [];
        }
        
        // 値が取得できていて未登録の場合、追加
        if(value['eikyou_nm'] && resDataFuguaiList[fuguai_nm].indexOf(value['eikyou_nm']['value']) == -1){
            resDataFuguaiList[fuguai_nm].push(value['eikyou_nm']['value'])
        }

        // 値が取得できていて未登録の場合、追加
        if(value['konpon_genin_nm'] && resDataKonponGeninList.indexOf(value['konpon_genin_nm']['value']) == -1){
            resDataKonponGeninList.push(value['konpon_genin_nm']['value'])
        }
    });

    return [resFuguaiNaiyou, resDataKoshouList, resDataFuguaiList, resDataKonponGeninList];
}

function createDot(resDataKoshouList:string[], resDataFuguaiList:FuguaiList, resDataKonponGeninList:string[]): string{
    let subgraphCnt = 0;
    let dot = '';

    // 故障
    for (const koshou of Object.values(resDataKoshouList)){
        dot += `subgraph cluster_${subgraphCnt++} {\n`;
        dot += `node [style=filled];\n`;
        dot += `"${koshou}";\n`;
        dot += `label = "故障";\n`;
        dot += `color=red\n`;
        dot += `}\n`;
	}

    // 不具合認知、影響
    for (const fuguai of Object.keys(resDataFuguaiList)){
        dot += `subgraph cluster_${subgraphCnt++} {\n`;

        // 不具合認知
        if(fuguai) {
            dot += `subgraph cluster_${subgraphCnt++} {\n`;
            dot += `node [style=filled];\n`;
            dot += `"${fuguai}";\n`;
            dot += `label = "不具合認知";\n`;
            dot += `color=green\n`;
            dot += `}\n`;
        }

        // 影響
        dot += `node [style=filled];\n`;
        dot += `"${resDataFuguaiList[fuguai].join('","')}";\n`;
        dot += `label = "影響";\n`;
        dot += `color=blue\n`;

        dot += `}\n`;
    }

    // 根本原因
    for (const konponGenin of Object.values(resDataKonponGeninList)){
        dot += `subgraph cluster_${subgraphCnt++} {\n`;
        dot += `node [style=filled];\n`;
        dot += `"${konponGenin}";\n`;
        dot += `label = "根本原因";\n`;
        dot += `color=orange\n`;
        dot += `}\n`;
	}

    return dot;
}

function getQueryDefect(fmid:string): string{
    const query = `
        PREFIX brick: <https://brickschema.org/schema/Brick#>
        PREFIX rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>
        PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>
        
        select distinct ?issue ?issue_nm ?fuguai_naiyou_val ?fuguai ?fuguai_nm ?koshou ?koshou_nm ?eikyou ?eikyou_nm ?konpon_genin ?konpon_genin_nm where{
            #issueを取得
            ?issue <http://www.semanticweb.org/chensisi/ontologies/2022/9/untitled-ontology-163:OWLObjectProperty_c91c8dae_12f9_45ab_ac9e_35a81e8c9dc3> "${fmid}"@ja. #FMID：検索キー
            ?issue rdfs:label ?issue_nm.
            
            #不具合内容取得
            OPTIONAL{
                ?issue <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLDataProperty_00000000000000000009> ?fuguai_naiyou_val.
                <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLDataProperty_00000000000000000009> rdfs:label ?fuguai_naiyou_prop_nm.
            }
            
            #不具合認知を取得
            OPTIONAL{
                ?issue <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLObjectProperty_00000000000000000003> ?fuguai.
                ?fuguai rdfs:label ?fuguai_nm.
                <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLObjectProperty_00000000000000000003> rdfs:label ?fuguai_prop_nm.
            }
            
            #故障を取得
            OPTIONAL{
                ?issue <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLObjectProperty_00000000000000000004> ?koshou.
                ?koshou rdfs:label ?koshou_nm.
                <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLObjectProperty_00000000000000000004> rdfs:label ?koshou_prop_nm.
            }
            
            #影響を取得
            OPTIONAL{
                ?issue <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLObjectProperty_00000000000000000005> ?eikyou.
                ?eikyou rdfs:label ?eikyou_nm.
                <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLObjectProperty_00000000000000000005> rdfs:label ?eikyou_prop_nm.
            }
            
            #根本原因を取得
            OPTIONAL{
                ?issue <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLObjectProperty_00000000000000000006> ?konpon_genin.
                ?konpon_genin rdfs:label ?konpon_genin_nm.
                <http://www.semanticweb.org/chensisi/ontologies/2023/0/untitled-ontology-188#OWLObjectProperty_00000000000000000006> rdfs:label ?konpon_genin_prop_nm.
            }
                
        }
    `;

    return query;
}